.section4 {
  .col-xs-12 {
    padding: 0px; }

  @import ".././assets/buttons/style.sass";
  height: 1079px;
  padding: 70px 17px;
  background: url("img/bg-color_4.png");
  width: 100%;
  p:first-child {
    font-size: 28px;
    text-align: center;
    font-weight: bold; }

  ul, p {
    padding-left: 0px;
    text-align: left;
    font-weight: 400;
    font-size: 16px; }

  .row {
    .col-xs-9 {
      font-size: 16px;
      vertical-align: middle; }
    .kek {
      position: relative;
      padding-top: 5px; }
    padding: 12px 0px;
    * {
      vertical-align: middle; }
    .lol {
      img {
        position: relative;
        padding-left: 5px; } } }



  .col-xs-12:nth-child(2) {
    display: flex;
    justify-content: center;
    img {
      width: 161px;
      height: 214px;
      margin-bottom: 50px; } }
  a {
    color: #3581d5;
    font-size: 14px;
    text-align: center; } }
